<template>
    <li>
        <span class="task" :class="{line:todo.completed, showanimation:todo.show}">
            <div class="mini-wrap">
                <p>{{index + 1}}</p>
            <input @change="todo.completed = !todo.completed,$emit('save-local')" :checked="check()" type="checkbox">
             <p class="title-task">{{todo.title}}</p> 
            </div>
            <div v-if="show" class="wrap-switch">
            <input class="left-item" v-model="todo.title" type="text">
            <button class="right-item"  @click="showVis" >	&#10003;</button>
            </div>
            <button class="more" @click="$router.push(`/todos/${todo.id}`)">Подробнее</button>
            <button class="btn-fix" @click="show = !show">&#9998;</button>
            <button class="btn-remove" v-on:click="$emit('remove-todo', todo.id), todo.show = true">&times;</button>
        </span>
    </li>
</template>
<script>
import contentTask from "@/components/contentTask"
import contentTodo from "@/views/contentTodo"
export default{
    props:["todo","todos","index","showTas"],
    data(){
    return{
        show:false
    }
},
components:{
contentTask,
contentTodo
},
methods:{
    check(){
        if(this.todo.completed == true){
            return true
        }
       if(this.todo.completed == false){
            return false
        }
    },
    showVis(){
      this.$emit('show-vis')
      this.show = false
    }
}
}
</script>
<style lang="scss" scoped>
@supports(-webkit-appearance: none) or (-moz-appearance: none) {
  input[type='checkbox'],
  input[type='radio'] {
    --active: #275EFE;
    --active-inner: #fff;
    --focus: 2px rgba(39, 94, 254, .3);
    --border: #BBC1E1;
    --border-hover: #275EFE;
    --background: #fff;
    --disabled: #F6F8FF;
    --disabled-inner: #E1E6F9;
    -webkit-appearance: none;
    -moz-appearance: none;
    height: 21px;
    outline: none;
    display: inline-block;
    vertical-align: top;
    position: relative;
    margin: 0;
    cursor: pointer;
    border: 1px solid var(--bc, var(--border));
    background: var(--b, var(--background));
    transition: background .3s, border-color .3s, box-shadow .2s;
    &:after {
      content: '';
      display: block;
      left: 0;
      top: 0;
      position: absolute;
      transition: transform var(--d-t, .3s) var(--d-t-e, ease), opacity var(--d-o, .2s);
    }
    &:checked {
      --b: var(--active);
      --bc: var(--active);
      --d-o: .3s;
      --d-t: .6s;
      --d-t-e: cubic-bezier(.2, .85, .32, 1.2);
    }
    &:disabled {
      --b: var(--disabled);
      cursor: not-allowed;
      opacity: .9;
      &:checked {
        --b: var(--disabled-inner);
        --bc: var(--border);
      }
      & + label {
        cursor: not-allowed;
      }
    }
    &:hover {
      &:not(:checked) {
        &:not(:disabled) {
          --bc: var(--border-hover);
        }
      }
    }
    &:focus {
      box-shadow: 0 0 0 var(--focus);
    }
    &:not(.switch) {
      width: 21px;
      &:after {
        opacity: var(--o, 0);
      }
      &:checked {
        --o: 1;
      }
    }
    & + label {
      font-size: 14px;
      line-height: 21px;
      display: inline-block;
      vertical-align: top;
      cursor: pointer;
      margin-left: 4px;
    }
  }
  input[type='checkbox'] {
    &:not(.switch) {
      border-radius: 7px;
      &:after {
        width: 5px;
        height: 9px;
        border: 2px solid var(--active-inner);
        border-top: 0;
        border-left: 0;
        left: 7px;
        top: 4px;
        transform: rotate(var(--r, 20deg));
      }
      &:checked {
        --r: 43deg;
      }
    }
    &.switch {
      width: 38px;
      border-radius: 11px;
      &:after {
        left: 2px;
        top: 2px;
        border-radius: 50%;
        width: 15px;
        height: 15px;
        background: var(--ab, var(--border));
        transform: translateX(var(--x, 0));
      }
      &:checked {
        --ab: var(--active-inner);
        --x: 17px;
      }
      &:disabled {
        &:not(:checked) {
          &:after {
            opacity: .6;
          }
        }
      }
    }
  }
  input[type='radio'] {
    border-radius: 50%;
    &:after {
      width: 19px;
      height: 19px;
      border-radius: 50%;
      background: var(--active-inner);
      opacity: 0;
      transform: scale(var(--s, .7));
    }
    &:checked {
      --s: .5;
    }
  }
}
.left-item{
     position: absolute;
    left: 20px;
    width: 50%;
    padding: 5px 15px;
    top: 0;
    bottom: 0;
    margin: auto 0;
    border-radius: 20px;
    border: none;
    height: 30px;
    font-size: 16px;
}
.left-item:focus{
border: none;
outline: none;
}
.left-item:active{
border: none;
outline: none;
}
.right-item{
   position: absolute;
    right: 20px;
    height: 40px;
    width: 40px;
    top: 0;
    bottom: 0;
    margin: auto 0;
    background: beige;
    color:green;
    font-weight: 800;
}
.wrap-switch{
    position: absolute;
    left: 0;
    width: 100%;
    height: 100%;
    background: #007BFF;
    z-index: 10;
    border-radius: 10px;
}
li{
    list-style-type: none;
}
.task {
    align-items: center;
    position: relative;
    padding: 20px;
    background: #FFFFFF;
    box-shadow: 0px 2px 8px rgb(0 0 0 / 14%);
    border-radius: 10px;
    display: flex;
    justify-content: space-between;
    margin-bottom: 20px;
    gap: 15px;
}
.title-task{
    width:160px;
    overflow:hidden;
    height: 16px;
}
button{
    color: #fff;
    background: #007BFF;
    border: none;
    border-radius: 90px;
    font-size: 20px;
    cursor: pointer;
}
.mini-wrap{
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 10px;
}
.btn-remove{
    padding: 4px 10px;
}
.btn-fix{
    padding: 2px 6px;
}
.more{
  font-size: 16px;
  padding: 8px 15px;
}
@media(max-width:500px){
.title-task{
  width: 90px;
}
.task{
  gap: 10px;
}
}
@media(max-width:400px){
.more{
  font-size:12px;
}
.title-task{
  font-size:14px;
}
.btn-remove{
  padding: 4px 8px;
}
button{
  font-size:14px;
}
.btn-fix{
  padding: 3px 5px;
}
.title-task{
    width:50px;
}
}
</style>